.firsk_screenfix_left {
  margin-left: calc((-100vw + 100%)/2);
  max-width: 1600px;
  @include max(991px) {
    max-width: 100%;
    margin: 0 auto;
  }
}
.aximo-yellow-bg{
    z-index: 0;
    position: relative;
    padding: 0 0 140px;
    @include max(991px) {
      padding: 100px 0;
    }
    @include max(767px) {
      padding: 80px 0;
    }
    &:before{
      content:"";
      left: 0;
      bottom: 0;
      width: 100%;
      height: 90%;
      z-index: -1;
      position: absolute;
      background-color: var(--yellow-bg);
      @include max(991px) {
        height: 100%;
      }
    }
  }
  
  

