.aximo-animation-thumb{
  left: -3px;
  visibility: hidden;
  position: relative;
  height: 730px;
  overflow: hidden;
  &.thumb2{
    left: 5px;
  }
  @include max(991px) {
    margin: 0 0 30px;
    left: 0;
  }
  @include max(1199px) {
    height: 700px;
  }
  @include max(767px) {
    height: auto;
  }
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center top;
  }
}

