.aximo-testimonial{
  &-wrap{
    padding: 30px;
    border-radius: 20px;
    margin-bottom: 30px;
    transition: all 0.4s;
    border: 2px solid var(--heading-color);
    &:hover{
      box-shadow: 4px 4px 0 0 var(--heading-color);
    }
  }
  &-rating{
    margin-bottom: 28px;
    ul{
      line-height: 1;
      li{
        margin: 0 4px;
        display: inline-block;
        i{
          font-size: 34px;
        }
        &:last-child{
          margin-right: 0;
        }
        &:first-child{
          margin-left: 0;
        }
      }
    }
  }
  &-data{
    h3{
      margin-bottom: 15px;
    }
  }
  &-author{
    display: flex;
    align-items: center;
    margin-top: 30px;
    &-thumb{
      margin-right: 12px;
      width: 70px;
      height: 70px;
      overflow: hidden;
      border-radius: 100%;
      border: 3px solid var(--heading-color);
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &-data{
      p{
        font-size: 21px;
        font-weight: 700;
        span{
          font-weight: 400;
        }
      }
    }
  }
}

.aximo-testimonial-column{
  @include max(991px) {
    margin-top: 40px;
  }
}
.aximo-testimonial-wrap2{
  border-radius: 0;
  padding: 0;
  padding-bottom: 30px;
  border: none;
  border-bottom: 1px solid var(--heading-color2);
  &:last-child{
    border: none;
    padding: 0;
    margin: 0;
  }
  &:hover{
    box-shadow: none;
  }
  .aximo-testimonial-rating{
    margin-bottom: 20px;
    ul{
      li{
        margin: 0;
      }
    }
  }
  .aximo-testimonial-data{
    p{
      font-size: 21px;
      font-weight: 600;
    }
  }
}

.aximo-default-content.aximo-sticky {
  position: sticky;
  top: 130px;
  @include max(991px) {
    position: static;
  }
}

.aximo-testimonial-wrap3{
  border: 10px;
  padding: 40px;
  margin-bottom: 24px;
  border: 2px solid #191931;
  &:hover{
    box-shadow: none;
  }
  .aximo-testimonial-quote{
    margin-bottom: 25px;
  }
  .aximo-testimonial-data{
    p{
      font-size: 21px;
      font-weight: 600;
      color: var(--heading-color3);
      @include max(767px) {
        font-size: 18px;
      }
    }
  }
  .aximo-testimonial-author-data{
    p{
      
      font-size: 18px;
      font-weight: 400;
      
    }
    span{
      font-weight: 600;
      margin-bottom: 10px;
    }
  }
  
}