.aximo-footer-top{
  .aximo-default-content {
    margin-right: 50px;
}
}

.aximo-footer-bottom{
  padding: 35px 0;
  border-top: 1px solid #333;
  @include max(991px) {
    padding: 30px;
  }
  &.two{
    border-top: 1px solid #E6E6E6;
  }
  &.three{
    border-top: 1px solid #E6E6E6;
  }
  &.three{
    border-top: 1px solid #302F43;
  }
}
.aximo-footer-logo{
  @include max(991px) {
    text-align: center;
    margin-bottom: 24px;
  }
}
.aximo-copywirht{
  text-align: right;
  @include max(991px) {
    text-align: center;
  }
  p{
    color: #999;
  }
  
}

.aximo-info {
  &-wrap{
    margin-top: 50px;
    margin-bottom: 50px;
    @include max(991px) {
      margin-top: 30px;
      margin-bottom: 30px;
    }
  }
  margin-bottom: 12px;
  &:last-child{
    margin-bottom: 0;
  }
  ul{
    li{
      font-size: 21px;
      color: var(--accent-color);
      a{
        color: var(--white-smoke);
      }
    }
  }
}

.aximo-hero-shape.aximo-footer-shape{
  bottom: 10%;
}
// End footer one


// footer two
.aximo-footer{
  &-section2{
    background-size: cover;
    background-position: center center;
  }
  &-textarea{
    @include max(1199px) {
      max-width: 600px;
      margin-bottom: 40px;
    }
    img{
      margin-bottom: 30px;
    }
    p{
      font-size: 21px;
    }
  }
  &-title{
    p{
      font-size: 21px;
      margin-bottom: 25px;
      font-weight: 600;
      @include max(767px) {
        margin-bottom: 17px;
      }
    }
    &.light{
      p{
        color: var(--light-color3);
      }
    }
  }
  &-menu{
    @include max(767px) {
      margin-bottom: 40px;
    }
    ul{
      li{
        margin: 14px 0;
        &:first-child{
          margin-top: 0;
        }
        &:last-child{
          margin-bottom: 0;
        }
        a{
          color: #2C2C2C;
          font-size: 21px;
          line-height: 30px;
          transition: all 0.4s;
          &:hover{
            color: var(--orange-color);
          }
        }
      }
    }
    &.light{
      ul{
        li{
          a{
            color: var(--light-color3);
            &:hover{
              color: var(--orange-color2);
            }
          }
        }
      }
    }
    &.extar-margin{
      margin-left: 50px;
      @include max(1199px) {
        margin-left: 0;
      }
    }
  }
}

.aximo-copywirht2{
  color: var(--header-color2);
  @include max(991px) {
    text-align: center;
  }
}

.aximo-footer-top2 {
  padding: 100px 0;
  @include max(767px) {
    padding: 80px 0;
  }
}
.aximo-footer-menu2{
  text-align: right;
  @include max(1199px) {
    text-align: center;
  }
  ul{
    li{
      display: inline-block;
      margin: 0 20px;
      @include max(767px) {
        margin: 0 10px;
      }
      &:first-child{
        margin-left: 0;
      }
      &:last-child{
        margin-right: 0;
      }
      a{
        font-weight: 600;
        color: var(--header-color);
      }
    }
  }
}

.aximo-copywright3{
  p{
    color: var(--header-color);
    font-weight: 600;
  }
  @include max(1199px) {
    text-align: center;
    margin-bottom: 20px;
  }
}

// footer 4
.aximo-subscription-wrap{
  padding: 80px 0;
  border-radius: 20px;
  background-color: #24243A;
}
.aximo-footer{
  &-section4{
    padding-top: 130px;
    background-color: #191931;
    @include max(991px) {
      padding-top: 100px;
    }
    @include max(767px) {
      padding-top: 80px;
    }
  }
  &-top4{
    padding: 80px 0;
  }
  &-textarea2{
    @include max(1199px) {
      max-width: 600px;
      margin-bottom: 40px;
    }
    img{
      margin-bottom: 30px;
    }
    p{
      font-size: 21px;
      color: var(--light-color3);
    }
  }
}

.aximo-copywirht4.light{
  p{
    color: var(--light-color3);
  }
}

.aximo-contact-info2{
  ul{
    li{
      margin: 24px 0;
      a{
        i{
          font-size: 20px;
          color: var(--orange-color2);
          margin-right: 10px;
        }
      }
    }
  }
}