.aximo-pricing{
  &-wrap{
    border-radius: 5px;
    margin-bottom: 30px;
    padding: 30px 25px 30px 30px;
    border: 2px solid var(--heading-color2);
  }
  &-header{
    margin-bottom: 20px;
    img{
      margin-bottom: 15px;
    }
    h3{
      font-family: $heading-font2;
      color: var(--heading-color2);
    }
  }
  &-price{
    h2{
      font-family: $heading-font2;
      color: var(--heading-color2);
    }
    padding-bottom: 25px;
    margin-bottom: 30px;
    border-bottom: 1px solid var(--heading-color2);
  }
  &-body{
    margin-bottom: 50px;
    ul{
      li{
        position: relative;
        font-size: 21px;
        font-weight: 600;
        padding-left: 40px;
        margin-bottom: 20px;
        @include max(1199px) {
          font-size: 18px;
        }
        &:last-child{
          margin-bottom: 0;
        }
        img{
          position: absolute;
          left: 0;
        }
      }
    }
  }
  &-btn{
    width: 100%;
    line-height: 55px;
    background-color: transparent;
    transition: all 0.4s;
    color: var(--heading-color2)!important;
    border: 2px solid var(--heading-color2);
    &:hover{
      color: var(--light-color1)!important;
      background-color: var(--orange-bg);
      border: 2px solid var(--orange-bg);
    }
    &.active{
      color: var(--light-color1)!important;
      background-color: var(--orange-bg);
      border: 2px solid var(--orange-bg);
    }
  }
}