.aximo-service-increase{
  &-wrap{
    margin: 0 50px;
    @include max(767px) {
      margin: 0;
    }
  }
  &-row{
    transition: all 0.4s;
    border-radius: 5px;
    padding: 0 20px;
    background-color: var(--light-bg2);
    &:hover{
      background-color: var(--heading-color2);
    }
    &:hover .aximo-service-increase-title h3, 
    &:hover .aximo-service-increase-body p{
      color: var(--light-color1);
    }
    &:hover .aximo-service-increase-icon i{
      color: var(--orange-color);
    }
    &:last-child .aximo-service-increase-item{
      border: none;
    }
  }
  &-item{
    max-width: 1292px;
    margin: 0 auto;
    padding: 50px 0;
    display: flex;
    align-items: center;
    border-bottom: 1px solid var(--heading-color2);

    @include max(850px) {
      display: block;
    }
  }
  &-title{
    flex: 0 0 30%;
    padding-right: 20px;
    @include max(850px) {
      padding: 0 0 20px;
    }
    h3{
      transition: all 0.4s;
      font-family: $heading-font2;
      color: var(--heading-color2);
    }
  }
  &-body{
    flex: 0 0 47%;
    padding-right: 20px;
    @include max(850px) {
      padding: 0 0 20px;
    }
    p{
      transition: all 0.4s;
    }
  }
  &-icon{
    flex: 0 0 23%;
    text-align: right;
    i{
      font-size: 48px;
      color: var(--heading-color2);
      transition: all 0.4s;
    }
  }
}

img.swipeimage {
  position: fixed;
  top: 0;
  left: 0;
  width: 294px;
  height: 245px;
  object-fit: cover;
  //transform: translateX(-50%) translateY(-50%);
  z-index: 9;
  opacity: 0;
  visibily: hidden;
  pointer-events: none;
  border-radius: 7px;
  border: 5px solid #fff;
}

