.aximo-project{
  &-thumb{
    overflow: hidden;
    border-radius: 20px;
    position: relative;
    border: 5px solid var(--white-smoke);
    img{
      transition: all 0.5s;
      transform: scale(1);
    }
    &:hover img{
      transform: scale(1.1);
    }
    &::before{
      content:"";
      left: 0;
      top: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      position: absolute;
      border-radius: 20px;
      transition: all 0.4s;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 35.42%, #000 100%);
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &-wrap{
    padding: 24px;
    bottom: 0;
    left: 0;
    z-index: 2;
    display: flex;
    align-items: center;
    position: absolute;
    @include max(767px) {
      display: block;
    }
  }
  &-data{
    flex: 0 0 85%;
    h3{
      margin-bottom: 10px;
      color: var(--white-smoke);
    }
    p{
      color: var(--white-smoke);
    }
  }
  &-icon{
    flex: 0 0 15%;
    text-align: right;
    @include max(767px) {
      margin-top: 20px;
      display: block;
      text-align: left;
    }
    svg path{
      transition: all 0.4s;
    }
    &:hover svg path{
      stroke: var(--accent-color);
    }
  }
}


.aximo-project-slider .swiper-pagination {
  max-width: 1296px;
  display: flex;
  position: static;
  margin: 0 auto;
  margin-top: 80px;
  justify-content: center;
  padding: 0 24px;
  @include max(991px) {
    margin-top: 60px;
  }
  @include max(767px) {
    margin-top: 40px;
  }
  

}
.aximo-project-slider .swiper-pagination-bullet {
  width: 25%;
  height: 3px;
  opacity: 1;
  border-radius: 0;
  margin: 0!important;
  transition: all .4s;
  background-color: #333;
}
.aximo-project-slider .swiper-pagination-bullet-active {
  background-color: var(--accent-bg);
 }

//  project one end

.aximo-project{
  &-section{
    border-radius: 20px;
    background-color: #191931;
  }
  &-thumb2{
    overflow: hidden;
    border-radius: 20px;
    position: relative;
    img{
      transition: all 0.5s;
      transform: scale(1);
    }
    &:hover img{
      transform: scale(1.1);
    }
    &:hover .aximo-project-icon2 img{
      transform: scale(1)!important;
    }
    &:hover .aximo-project-icon2{
      top: 50%;
      opacity: 1;
      visibility: visible;
    }
    &:hover:before{
      opacity: 1;
    }
    &::before{
      content:"";
      left: 0;
      top: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      opacity: 0;
      position: absolute;
      border-radius: 10px;
      transition: all 0.4s;
      border-radius: 10px;
      background: linear-gradient(0deg, rgba(25, 25, 49, 0.60) 23.47%, rgba(25, 25, 49, 0.00) 102.43%);
      mix-blend-mode: multiply;
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &-data2{
    margin-top: 24px;
    padding-right: 30px;
    h3{
      margin-bottom: 15px;
      color: var(--light-color3);
      font-family: $heading-font4;
      @include max(767px) {
        font-size: 24px;
      }
    }
    p{
      font-size: 21px;
      color: var(--light-color3);
      @include max(767px) {
        font-size: 18px;
      }
    }
  }
  &-icon2{
    top: 55%;
    left: 50%;
    width: 80px;
    height: 80px;
    display: flex;
    z-index: 1;
    opacity: 0;
    transition: all 0.4s;
    visibility: hidden;
    align-items: center;
    position: absolute;
    border-radius: 100%;
    justify-content: center;
    transform: translate(-50%, -50%);
    background-color: var(--light-bg4);
    img{
      width: auto;
      height: auto;
      
    }
    
  }
}


.aximo-project-slider2{
  padding-bottom: 11px;
  @include max(767px) {
    margin: 0 12px;
  }

  .swiper-scrollbar.swiper-scrollbar-horizontal{
    background-color: #666;
    position: relative;
    margin: 0 auto;
    height: 2px;
    left: 0;
    width: 100%;
    max-width: 1296px;
    margin-top: 100px;
    @include max(991px) {
      margin-top: 80px;
    }
    @include max(767px) {
      margin-top: 60px;
    }
  }
  .swiper-scrollbar-drag{
    cursor: pointer;
    width: 85px!important;
    height: 50px;
    top: -24px;
    left: -2px;
    position: absolute;
    background-size: cover;
    background-color: transparent;
    background-image: url(../images/v4/arrow.png);
  }
}
